import { Component, OnInit } from '@angular/core';
import { PopoverController } from '@ionic/angular';
import { MenuPage } from '../menu/menu.page';

@Component({
  selector: 'app-filter',
  templateUrl: './filter.page.html',
  styleUrls: ['./filter.page.scss'],
})
export class FilterPage implements OnInit {

  constructor(public popoverController: PopoverController) { }

  genderShow: boolean | undefined;
  pricesShow: boolean | undefined;
  avgShow: boolean | undefined;
  colorShow: boolean | undefined;
  concernShow: boolean | undefined;
  concernfilterShow: boolean | undefined;

  ngOnInit() {
    this.genderShow = true;
    this.pricesShow = true;
    this.avgShow = true;
    this.colorShow = true;
    this.concernShow = true;
    this.concernfilterShow = true;
  }
  async menuOpen() {
    const popover = await this.popoverController.create({
      component: MenuPage,
      cssClass: 'menuList',
    });
    await popover.present();
  }

  gender() {
    if (this.genderShow == true) {
      this.genderShow = false;
    } else {
      this.genderShow = true;
    }
  }

  price() {
    if (this.pricesShow == true) {
      this.pricesShow = false;
    } else {
      this.pricesShow = true;
    }
  }

  avg() {
    if (this.avgShow == true) {
      this.avgShow = false;
    } else {
      this.avgShow = true;
    }
  }

  color() {
    if (this.colorShow == true) {
      this.colorShow = false;
    } else {
      this.colorShow = true;
    }
  }

  concern() {
    if (this.concernShow == true) {
      this.concernShow = false;
    } else {
      this.concernShow = true;
    }
  }

  concernfilter() {
    if (this.concernfilterShow == true) {
      this.concernfilterShow = false;
    } else {
      this.concernfilterShow = true;
    }
  }

}
